import Vue from 'vue'
import Router from 'vue-router'
import Home from './pages/Home'
import Error404 from './pages/404'

Vue.use(Router)

const router = new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'home',
            component: Home
        },
        {
            path: '*',
            name: '404',
            component: Error404
        }
    ]
})

export default router;